import "./Home.css";
import { useEffect, useState, useMemo, lazy, Suspense } from "react";
import { useSelector, useDispatch } from "react-redux";

import { sortAnimes, searchAnimes } from "../../redux/webSlice";
import PuffLoader from "react-spinners/PuffLoader";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

const AnimeCard = lazy(() => import("../../components/AnimeCard/AnimeCard"));

const Home = () => {
  //! Initializations
  const dispatch = useDispatch();
  const { animes, user, sortedAnimes, searchedAnimes, filteredAnimes } =
    useSelector((state) => ({
      ...state.web,
    }));
  //! -----------------------------------------------------------

  //! useStates
  const [popularAnimes, setPopularAnimes] = useState([]);
  const [allArr, setAllArr] = useState([]);

  let isLogged = useMemo(() => localStorage.getItem("isLogged"), []);

  //! -----------------------------------------------------------

  //! useEffects
  useEffect(() => {
    setPopularAnimes(
      [...animes]
        .sort((a, b) => b.views - a.views)
        .filter((arr, index) => index < 10)
    );
  }, [animes]);

  useEffect(() => {
    let anims = [...animes];
    if (filteredAnimes === "") {
      anims.sort((a, b) => b?.likes?.length - a?.likes?.length);
    } else if (filteredAnimes === "") {
      anims.sort((a, b) => b?.views - a?.views);
    } else {
      anims.sort((a, b) => b?.createdAt - a?.createdAt);
    }
    setAllArr(anims);
  }, [animes, filteredAnimes]);

  useEffect(() => {
    if (sortedAnimes.length === 0) {
      setAllArr(animes);
    }
  }, [sortedAnimes.length]);

  useEffect(() => {
    dispatch(searchAnimes(animes));
  }, [animes]);

  useEffect(() => {
    setAllArr(searchedAnimes);
  }, [searchedAnimes.length]);

  useEffect(() => {
    if (sortedAnimes.length === 0) return;
    let foundAnime = [];
    sortedAnimes.forEach((output) => {
      animes?.forEach((outputTwo) => {
        if (outputTwo?.genres?.includes(output)) {
          foundAnime.push(outputTwo);
        }
      });
    });
    if (foundAnime === undefined) return setAllArr([]);
    setAllArr(foundAnime);
  }, [sortedAnimes, animes]);

  // useEffect(() => {
  //   user?.watchLater?.sort((a, b) => b?.anime?.views - a?.anime?.views);
  // }, [user]);

  //! -----------------------------------------------------------

  return (
    <main id="home">
      {Object.values(animes).length !== 0 ? (
        <>
          <section id="home_popular">
            {sortedAnimes.length === 0 && (
              <>
                <h1></h1>
                <Swiper
                  slidesPerView={3}
                  spaceBetween={33}
                  slidesPerGroup={1}
                  autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                  }}
                  loop={true}
                  loopFillGroupWithBlank={true}
                  breakpoints={{
                    100: {
                      slidesPerView: 1,
                    },
                    430: {
                      slidesPerView: 2,
                    },
                    640: {
                      slidesPerView: 3,
                    },
                    950: {
                      slidesPerView: 4,
                    },
                    1000: {
                      slidesPerView: 3,
                    },
                    1220: {
                      slidesPerView: 4,
                    },
                    1660: {
                      slidesPerView: 5,
                    },
                    2250: {
                      slidesPerView: 6,
                    },
                  }}
                  navigation={true}
                  modules={[Autoplay, Navigation]}
                  className="mySwiper"
                >
                  {popularAnimes?.map((anime, index) => (
                    <SwiperSlide>
                      <Suspense fallback={<div>Loading...</div>}>
                        <AnimeCard key={index} anime={anime} />
                      </Suspense>
                    </SwiperSlide>
                  ))}
                </Swiper>
              </>
            )}
          </section>
          <article className="ADMain"> </article>
          {user?.watchLater?.length >= 5 ? (
            <section id="home_last_played">
              {isLogged === "true" &&
                sortedAnimes.length === 0 &&
                Object.values(user).length !== 0 &&
                user?.watchLater.length !== 0 && (
                  <>
                    <h1> </h1>
                    <Swiper
                      slidesPerView={3}
                      spaceBetween={33}
                      slidesPerGroup={1}
                      autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                      }}
                      loop={true}
                      loopFillGroupWithBlank={true}
                      breakpoints={{
                        100: {
                          slidesPerView: 1,
                        },
                        430: {
                          slidesPerView: 2,
                        },
                        640: {
                          slidesPerView: 3,
                        },
                        950: {
                          slidesPerView: 4,
                        },
                        1000: {
                          slidesPerView: 3,
                        },
                        1220: {
                          slidesPerView: 4,
                        },
                        1660: {
                          slidesPerView: 5,
                        },
                        2250: {
                          slidesPerView: 6,
                        },
                      }}
                      navigation={true}
                      modules={[Autoplay, Navigation]}
                      className="mySwiper"
                    >
                      {Object.values(user).length !== 0 &&
                        user?.watchLater?.map((output, index) => (
                          <SwiperSlide>
                            <Suspense fallback={<div>Loading...</div>}>
                              <AnimeCard key={index} anime={output?.anime} />
                            </Suspense>
                          </SwiperSlide>
                        ))}
                    </Swiper>
                  </>
                )}
            </section>
          ) : (
            <section id="home_last_played">
              {isLogged === "true" &&
                sortedAnimes.length === 0 &&
                Object.values(user).length !== 0 &&
                user?.watchLater.length !== 0 && (
                  <>
                    <h1> </h1>
                    <article>
                      {Object.values(user).length !== 0 &&
                        user?.watchLater?.map((output, index) => (
                          <Suspense fallback={<div>Loading...</div>}>
                            <AnimeCard key={index} anime={output?.anime} />
                          </Suspense>
                        ))}
                    </article>
                  </>
                )}
            </section>
          )}
          <article className="ADMain"> </article>
          <section id="all">
            <div style={{ display: "flex", gap: "1rem" }}>
              <h1
                style={{ cursor: "pointer" }}
                onClick={async () => {
                  await dispatch(sortAnimes(animes));
                  await setAllArr(animes);
                }}
              >
                 
              </h1>
              {sortedAnimes?.length !== 0 && (
                <button
                  onClick={() => {
                    setAllArr(animes);
                    dispatch(sortAnimes([]));
                  }}
                >
                   
                </button>
              )}
            </div>

            <article>
              {allArr?.map((anime, index) => (
                <Suspense fallback={<div>Loading...</div>}>
                  <AnimeCard key={index} anime={anime} />
                </Suspense>
              ))}
            </article>
            {allArr.length === 0 && (
              <span className="home_anime_filter_text">
                  
              </span>
            )}
          </section>
          <article className="ADMain"> </article>
        </>
      ) : (
        <PuffLoader
          className="profileHandler"
          color="#d084e3"
          loading={true}
          size={500}
          speedMultiplier={1}
        />
      )}
      {/* <button> </button> */}
      <article className="ADFirst"> </article>
      <article className="ADSecond"> </article>
    </main>
  );
};

export default Home;
